<template>
  <div class="straightBasket">
    <v-container fluid>
      <!-- 語系選單(需隱藏) -->
      <SelectSelectLanguage v-show="false"/>

      <div class="pt-1">
        <!-- <input onclick="window.close();" value="關閉視窗" type="button"> -->
        複合過關-{{ $route.query.gameType }}注單下注狀況&nbsp;目前共有{{ pagination.totalItems }}筆資料
        <v-btn
          small
          color="grey"
          @click="closeWin()">關閉視窗</v-btn>

        <v-data-table
          :headers="headers"
          :items="BillList"
          no-data-text="目前無資料"
          :pagination.sync="pagination"
          :dark="false"
          :default-sort="true"
          class="elevation-1 mt-2"
          hide-actions>
          <template
            slot="items"
            slot-scope="props">
            <td class="text-xs-center">{{ props.item.sn }}</td>
            <td class="text-xs-center">{{ props.item.m_id }}</td>
            <td class="text-xs-center"><div v-html="props.item.matter"></div>{{ props.item.all_count }}&nbsp;{{ props.item.m_date}}<br>{{ props.item.ip }}</td>
            <td class="text-xs-center">{{ props.item.gold }}<br>
            <span v-if="props.item.end===1" 
              :class="[{'red--text': parseFloat(props.item.result_gold) < 0},
                    {'green--text': parseFloat(props.item.result_gold) > 0}]"
            >{{ props.item.result_gold }}</span>
            </td>
          </template>
        </v-data-table>

        <div v-if="pages > 0" class="text-xs-center pt-2">
          <v-pagination
            v-model="pagination.currentPage"
            :length="pages"
            :total-visible="pagination.totalVisible"
            color="deep-orange darken-1"
            @input="loadNextPage"/>
        </div>
      </div>
    </v-container>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import Amount from '@/mixins/sport/Amount'

export default {
  name: 'Amount',
  mixins:[Amount],
  data () {
    return {
      gamelist: {
        ball: 12
      }
    }
  },
}
</script>
