
<script>
import { mapGetters, mapActions } from 'vuex'

export default {
  data () {
    return {
      showNoData:'目前無任何資料',
      headers: [
        { text: '單號', value: 'number', sortable: false, align: 'center' },
        { text: '會員', value: 'member', sortable: false, align: 'center' },
        { text: '內容', value: 'detail', sortable: false, align: 'center' },
        { text: '金額', value: 'amount', sortable: false, align: 'center' }
      ],
      // 表格內容
      BillList: [],
      pagination: {
        currentPage: 1, // 當前頁數
        rowsPerPage: 10, // 每頁筆數
        totalItems: 0, // 總筆數
        totalVisible: 10 // 顯示最大可見頁數按鈕，其他會用 ... 顯示
      },
      // 頁面載入狀態
      loadingStatus: false,
      gamelist: {
        act: 'allup',
        game_serial_number: '',
        type: '', // 從過關傳過來的玩法
        bet_team: '',
        posting: '',
        ball: 0,
        page: 1,
        count: 1
      }
    }
  },
  computed: {
    ...mapGetters({
      getBillListState: 'sports/getBillListState'
    }),
    pages () {
      if (this.pagination.rowsPerPage == null || this.pagination.totalItems == null) return 0
      return Math.ceil(this.pagination.totalItems / this.pagination.rowsPerPage)
    }
  },
  watch: {
    getBillListState (payload) {
      // console.log(payload)
      this.BillList = payload.result.data_list
      // 若 API 有當前頁數回傳資料，可設定如下
      // this.pagination.currentPage = payload.result.current_page
      this.pagination.totalItems = payload.result.total_count
      // this.BillList.contents = payload.result.list // ?不清楚用處?
    },
    'pagination.rowsPerPage' (rowsPerPage) {
      // 回第一頁
      this.pagination.currentPage = 1
      this.loadBillList()
    }
  },
  mounted () {
    // 從過關傳入的玩法type
    this.gamelist.type = this.$route.query.type
    this.loadBillList()
  },
  methods: {
    ...mapActions({
      getBillList: 'api/getBillList'
    }),
    // 載入玩法金額資料
    loadBillList (page, rowsPerPage) {
      // 載入狀態(開始)
      this.loadingStatus = true
      let params = {
        act: this.gamelist.act,
        game_serial_number: this.$route.query.gameSN,
        type: this.$route.query.type,
        bet_team: this.$route.query.bet_team,
        posting: this.$route.query.Posting,
        ball: this.gamelist.ball,
        page: page !== undefined ? page : this.pagination.currentPage,
        count: rowsPerPage !== undefined ? rowsPerPage : this.pagination.rowsPerPage
      }
      this.getBillList(params)
        .then(res => {
          if (res.status === 200) {
            // 載入狀態(結束)
            this.loadingStatus = false
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    // 載入下一頁
    loadNextPage (page) {
      // 若 API 有當前頁數回傳資料，可註解以下
      this.pagination.currentPage = page
      this.getBillList()
    },
    closeWin () {
      window.close()
    }
  }
}
</script>
